.features_wrapp {
  padding-bottom: 335px; }
.features_container {
  h2 {
    font-size: 72px;
    font-family: "impact";
    font-weight: 400;
    color: #25365d;
    text-align: center;
    padding-top: 150px; } }
.img_features {
  // сделать по центру по правильному
  position: absolute;
  margin-left: 170px;
  padding-top: 145px; }

.features_coll1 {
  display: grid;
  grid-template-columns: 150px 150px;
  grid-template-rows: 50px 50px;
  justify-content: space-between;
  margin-top: 185px;
  grid-gap: 140px;
  line-height: 20px; }
.text_edit {
  font-family: "MariadPro";
  line-height: 22px;
  h3 {
    font-size: 22px;
    color: #25365d;
    font-family: "MariadPro";
    font-weight: 600;
    &::after {
      border-right: 2px solid black; } } }
